@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  /* overflow-x: hidden; */
}

body {
  margin: 0;
  font-family: 'Mulish', sans-serif, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#header .sidebar {
  display: flex !important;
  justify-content: center;
  align-items: center;
  padding-top: 5rem;
}

#header .sidebar .sidebar1 {
  flex: 0.5 1;
}

#header .sidebar .sidebar1 .get-started {
  background-color: white !important;
  border: 1px solid teal;
  border-radius: 15px;
}

#header .sidebar .sidebar1 .get-started:hover {
  background-color: teal !important;
  color: white;
}

#header .sidebar .sidebar2 {
  flex: 0.5 1;
}

#header .sidebar .sidebar2 img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

#header .sidebar .brand-name {
  color: teal;
  white-space: nowrap;
  font-weight: bold;
}

@media (max-width: 480px) {
  #header .sidebar {
    flex-direction: column-reverse;
  }
}

@media (max-width: 768px) {
  #header .sidebar {
    flex-direction: column-reverse;
  }
}

.nav_bg {
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 0;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  z-index: 999;
  background-color: whitesmoke !important;
}

.navbar .navbar-brand {
  font-size: 2rem;
}

.navbar .navbar-brand:hover {
  font-weight: bold;
  color: teal !important;
}

.navbar .menu_active {
  font-weight: bold;
  border-bottom: 1px solid green;
}

@media (max-width: 480px) {
  .dash {
    min-width: 100% !important;
    margin-left: 0 !important;
  }
  .navbar .navbar-brand {
    font-size: 1.6rem;
  }
}

.card {
  min-height: 450px !important;
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.card .btn-contact {
  position: fixed !important;
  right: 20px;
  bottom: 1rem;
}

footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
}

.about, .home, .contact, .service {
  min-height: 100vh;
}
